import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AllMiscComponent } from './all-misc/all-misc.component';
import { HighChartsExampleComponent } from './high-charts-example/high-charts-example.component';
import { FormExComponent } from './component/form-ex/form-ex.component';

import { ChangeDetectionComponent } from './component/change-detection/change-detection.component';
import { DefaultCdComponent } from './component/change-detection/default-change-detection/default-cd/default-cd.component';
import { OnPushChangeDetectionComponent } from './component/change-detection/on-push-change-detection/on-push-change-detection.component';
import { ManualChangeDetectionComponent } from './component/change-detection/manual-change-detection/manual-change-detection.component';

const routes: Routes = [
  { path: '', redirectTo: '/misc', pathMatch: 'full' },
  { path: 'misc', component: AllMiscComponent },
  { path: 'highcharts', component: HighChartsExampleComponent },
  { path: 'forms', component: FormExComponent },
  { path: 'change-detection', component: ChangeDetectionComponent,
    children : [
      { path: '', redirectTo: 'default-change-detection', pathMatch: 'full'},
      { path: 'default-change-detection', component: DefaultCdComponent},
      { path: 'on-push-change-detection', component: OnPushChangeDetectionComponent},
      { path: 'manual-change-detection', component: ManualChangeDetectionComponent}
  ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


