import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'creditCard'
})
export class CreditCardPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    console.log(args);
    value = value.toString();
    var masked = value.replace(/\d(?=\d{4})/g, args ? args : '*');
    return masked;
  }

}



