import {Component, NgZone, ElementRef, Input} from '@angular/core';
import {Subject} from 'rxjs/Rx';
import {toggleClass} from '../../toggle-class.service';

@Component({
  selector: 'cmp-manual-det-one',
  template: `
    <a>Cmp1</a>

    <ul>
      <li><cmp-manual-det-two [notifier]="notifier"></cmp-manual-det-two></li>
      <li><cmp-manual-det-three></cmp-manual-det-three></li>
    </ul>
  `
})
export class ComponentManualDetectOne {

  @Input() notifier:Subject<any>;

  constructor(private zone: NgZone, private el: ElementRef) {}

  ngAfterViewChecked() {
    toggleClass(this.el, this.zone);
  }
}



