export function toggleClass(el, zone, className = 'checked') {
  let a = el.nativeElement.querySelector('a');
  a.classList.add(className);
  console.log(a);
  zone.runOutsideAngular(() => {
    setTimeout(() => {
      a.classList.remove(className);
    }, 700);
  })
}




