import {Component, NgZone, ElementRef, ChangeDetectionStrategy} from '@angular/core';
import {toggleClass} from '../../toggle-class.service';

@Component({
  selector: 'cmp-on-push-eight',
  template: `
    <a class="on-push">Cmp8</a>

    <ul>
      <li><cmp-on-push-sixteen></cmp-on-push-sixteen></li>
    </ul>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ComponentOnPushEight {

  constructor(private zone: NgZone, private el: ElementRef) {}

  ngDoCheck() {
    toggleClass(this.el, this.zone);
  }
}



