import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-form-ex',
  templateUrl: './form-ex.component.html',
  styleUrls: ['./form-ex.component.css']
})
export class FormExComponent implements OnInit {
  value:number;

  constructor() {
    this.value = 1;
  }

  ngOnInit() {
  }

  inc() {
    this.value++;
  }

  dec() {
    this.value--;
  }
}



