import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-high-charts-example',
  templateUrl: './high-charts-example.component.html'
})
export class HighChartsExampleComponent implements OnInit {

  options: Object;

  constructor() {
    this.options = {
        chart: {
            type: 'column'
        },
        title: {
            text: ''
        },
        xAxis: {
            categories: ['Nov', 'Dec', 'Jan', 'Feb', 'Mar'],
            gridLineColor: '#E4E5E3',
            gridLineWidth: 1,
            crosshair: {
                color: '#E4E5E3'
            }
        },
        yAxis: {
            min: 0,
            max: 20,    //calc radomly based on the values
            labels: {
                enabled: false
            },
            gridLineColor: 'white'
        },
        legend: {
            align: 'left',
            x: 15,
            verticalAlign: 'top',
            y: -15,
            floating: true,
            backgroundColor: 'white',
            shadow: false
        },
        plotOptions: {
            column: {
                stacking: 'normal'
            }
        },
        tooltip: {
            enabled: false
        },
        credits: {
            enabled: false
        },
        series: [{
            name: 'Paid',
            data: [3, 3, 4, 2, 2],
            color: '#9BD7BC',
            maxPointWidth: 40
        }, {
            name: 'Reversal',
            data: [2, 2, 3, 2, 1],
            color: '#FFD1CF',
            maxPointWidth: 40
        },{
            name: 'Total Due',
            type: 'spline',
            data: [2, 1, 2, 3, 4],
            color: '#ABE0F9'
        }]
    };

  }

  ngOnInit() {
  }

  onSeriesClick(e) {
    console.log(e);
    //this.options.xAxis.crosshair.color = '#E4E5E3';
  }

}



