import { Component, OnInit, ElementRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'app-ctrl-value-ex',
  templateUrl: './ctrl-value-ex.component.html',
  styleUrls: ['./ctrl-value-ex.component.css'],
  providers: [
    { 
      provide: NG_VALUE_ACCESSOR,
      useExisting: CtrlValueExComponent,
      multi: true
    }
  ]
})
export class CtrlValueExComponent implements OnInit, ControlValueAccessor {
  propagateChange:any = (val) => {
     console.log('propagateChange-->', val);
  };
  _counterValue = 0;
  el:any;
  fromCompValue:any;

  constructor(el: ElementRef) {
    this.el = el;
  }

  ngOnInit() {
  }

  get counterValue() {
    return this._counterValue;
  }

  set counterValue(val) {
    this._counterValue = val;
    this.propagateChange(this._counterValue+'hi');
  }

  //from Model -> DOM
  writeValue(value:any) {
    //this.counterValue = value;
    console.log('writevValue-->', value);
    this.fromCompValue = value;
  }

  //from DOM -> Model
  registerOnChange(fn:any) {
    this.propagateChange = fn;
  }

  registerOnTouched(fn:any) {}

  increase() {
    this.counterValue++;
  }

  decrease() {
    this.counterValue--;
  }

  onTextChange(e) {
    let val = e.target.value;
    console.log(val);
    this.el.innerHTML = 'chang';
    if (!isNaN(val)) {
      this.propagateChange(parseInt(val));
    }
    
  }

}



